<template>
  <div id="projectMonitoring"
       v-loading="loading"
       element-loading-text="拼命加载中，请稍后..."
       element-loading-spinner="el-icon-loading"
       element-loading-background="#9f9f9f91">
    <el-row class="project-select">
      <!-- 1.选择项目 -->
      <project-select />
    </el-row>
    <el-row class="project-monitoring">
      <el-row type="flex"
              class="title content-title"
              align="middle">
        云端电站项目监控
      </el-row>
      <!-- 2.项目数据 -->
      <el-row class="project-data">
        <el-row class="title">项目数据</el-row>
        <el-row :gutter="20"
                type="flex"
                class="card-box-head">
          <el-col :span="12">
            <el-card class="box-card">
              <el-row>
                <el-col :span="6">
                  <el-row class="card-box-head-label">
                    <el-col>机组总数</el-col>
                    <el-col>5</el-col>
                  </el-row>
                </el-col>
                <el-col :span="6">
                  <el-row class="card-box-head-label">
                    <el-col>单机台数</el-col>
                    <el-col>5</el-col>
                  </el-row>
                </el-col>
                <el-col :span="6">
                  <el-row class="card-box-head-label">
                    <el-col>并机台数</el-col>
                    <el-col>5</el-col>
                  </el-row>
                </el-col>
                <el-col :span="6">
                  <el-row class="card-box-head-label">
                    <el-col>合闸总数</el-col>
                    <el-col>5</el-col>
                  </el-row>
                </el-col>
              </el-row>
            </el-card>
          </el-col>
          <el-col :span="12">
            <el-card class="box-card">
              <el-row>
                <el-col :span="4"
                        v-for="(item, key, index) in stateObj"
                        :key="index">
                  <el-row class="card-box-head-label2">
                    <el-col :style="{ color: item }">{{
                      stateNum[key]
                    }}</el-col>
                    <el-col>{{ key }}</el-col>
                  </el-row>
                </el-col>
              </el-row>
            </el-card>
          </el-col>
        </el-row>
        <el-row :gutter="20"
                type="flex"
                class="card-box-bottom">
          <el-col :span="12">
            <el-row :gutter="15">
              <el-col :span="12">
                <el-card class="box-card">
                  <el-row class="card-box-bottom-label">
                    <el-col>
                      <el-row>
                        <el-col :span="12">实际总功</el-col>
                        <el-col :span="12">总功率</el-col>
                      </el-row>
                    </el-col>
                    <el-col>
                      <el-row>
                        <el-col :span="12">5</el-col>
                        <el-col :span="12">6</el-col>
                      </el-row>
                    </el-col>
                    <el-col>机组群容量： 0.00</el-col>
                  </el-row>
                </el-card>
              </el-col>
              <el-col :span="12">
                <el-card class="box-card">
                  <el-row class="card-box-bottom-label">
                    <el-col>供电时长</el-col>
                    <el-col>5</el-col>
                    <el-col>上次供电时长： 0.00</el-col>
                  </el-row>
                </el-card>
              </el-col>
            </el-row>
          </el-col>
          <el-col :span="12">
            <el-card class="box-card">
              <el-table :data="tableData"
                        :show-header="false"
                        style="width: 100%"
                        :height="120"
                        :cell-style="{
                  padding: '0',
                  fontSize: '.7rem'
                }">
                <el-table-column prop="mchCode"
                                 label="机组编码"
                                 width="1">
                </el-table-column>
                <el-table-column prop="content"
                                 label="报警内容"
                                 width="3">
                </el-table-column>
                <el-table-column prop="time"
                                 label="报警时间"
                                 width="1">
                </el-table-column>
                <el-table-column label="操作"
                                 width="1">
                  <template slot-scope="scope">
                    <el-button @click="operatingHandler(scope.row)"
                               type="text">故障诊断</el-button>
                  </template>
                </el-table-column>
              </el-table>
            </el-card>
          </el-col>
        </el-row>
      </el-row>
      <!-- 3.机组运行数据 -->
      <el-row class="operating-data">
        <el-row class="title">机组运行数据</el-row>
        <!-- 3.1.选择机组 -->
        <el-row class="mch-select">
          <mch-select @searchHandler="searchHandler" />
        </el-row>
        <!-- 3.2.机组信息列表 -->
        <el-row class="mch-info-table">
          <el-checkbox v-model="checked"
                       :disabled="disabled">展示并机组分机搜索结果</el-checkbox>
          <transverse-table :mchData="mchDataAll" />
        </el-row>
        <!-- 3.3.跳转到项目详细数据 -->
        <el-row class="to-project-detail"
                type="flex"
                justify="end">
          <el-button type="text"
                     @click="checkDetailHandler">>>查看详细数据</el-button>
        </el-row>
      </el-row>
    </el-row>
  </div>
</template>

<script>
import ProjectSelect from '@/components/ProjectSelect'
import MchSelect from '@/components/ToggleCrewNoList'
import TransverseTable from '@/components/TransverseTable'

export default {
  name: 'projectMonitor',
  components: {
    ProjectSelect,
    MchSelect,
    TransverseTable
  },
  data () {
    return {
      // 机组状态的颜色
      stateObj: {
        报警: '#f1534f',
        警告: '#f2c614',
        运行: '#20be62',
        停机: '#f1534f',
        离线: '#808080',
        全部: '#000000'
      },
      // 机组状态信息
      stateNum: {
        报警: 0,
        警告: 1,
        运行: 2,
        停机: 3,
        离线: 4,
        全部: 10
      },
      // 报警数据（第四个卡片 table 数据）
      tableData: [
        {
          mchCode: 'FCZZL-21001',
          content: '发电A相电压低停机',
          time: '2022-1-12 11:22:01'
        },
        {
          mchCode: 'FCZZL-21001',
          content: '发电A相电压低停机',
          time: '2022-1-12 11:22:01'
        },
        {
          mchCode: 'FCZZL-21001',
          content:
            '发电A相电压低停机发电A相电压低停机发电A相电压低停机发电A相电压低停机',
          time: '2022-1-12 11:22:01'
        },
        {
          mchCode: 'FCZZL-21001',
          content: '发电A相电压低停机',
          time: '2022-1-12 11:22:01'
        }
      ],
      // 机组数据
      mchData: [
        {
          mchCode: 'FPSS9487595950',
          mchType: '单机组',
          mchTotal: 1,
          mchState: '正常运行',
          mchPosition: '成都市武侯区武汉大路',
          mchLoadTotal: 3,
          // 是否分机组
          hasSub: false
        },
        {
          mchCode: 'FPSS9487595950',
          mchType: '单机组',
          mchTotal: 1,
          mchState: '警告',
          mchPosition: '成都市武侯区武汉大路',
          mchLoadTotal: 3,
          // 是否分机组
          hasSub: false
        },
        {
          mchCode: 'FPSS9487595950',
          mchType: '并机组主机1',
          mchTotal: 1,
          mchState: '停机',
          mchPosition: '成都市武侯区武汉大路',
          mchLoadTotal: 3,
          // 是否分机组
          hasSub: true,
          subMch: [
            {
              mchCode: 'FPSS9487595950',
              mchType: '并机组1分机1',
              mchTotal: 1,
              mchState: '正常运行',
              mchPosition: '成都市武侯区武汉大路',
              mchLoadTotal: 3
            },
            {
              mchCode: 'FPSS9487595950',
              mchType: '并机组1分机2',
              mchTotal: 1,
              mchState: '离线',
              mchPosition: '成都市武侯区武汉大路',
              mchLoadTotal: 3
            },
            {
              mchCode: 'FPSS9487595950',
              mchType: '并机组1分机3',
              mchTotal: 1,
              mchState: '报警',
              mchPosition: '成都市武侯区武汉大路',
              mchLoadTotal: 3
            }
          ]
        },
        {
          mchCode: 'FPSS9487595950',
          mchType: '单机组',
          mchTotal: 1,
          mchState: '警告',
          mchPosition: '成都市武侯区武汉大路',
          mchLoadTotal: 3,
          // 是否分机组
          hasSub: false
        },
        {
          mchCode: 'FPSS9487595950',
          mchType: '并机组主机2',
          mchTotal: 1,
          mchState: '停机',
          mchPosition: '成都市武侯区武汉大路',
          mchLoadTotal: 3,
          // 是否分机组
          hasSub: true,
          subMch: [
            {
              mchCode: 'FPSS9487595950',
              mchType: '并机组分机1',
              mchTotal: 1,
              mchState: '正常运行',
              mchPosition: '成都市武侯区武汉大路',
              mchLoadTotal: 3
            },
            {
              mchCode: 'FPSS9487595950',
              mchType: '并机组分机2',
              mchTotal: 1,
              mchState: '离线',
              mchPosition: '成都市武侯区武汉大路',
              mchLoadTotal: 3
            },
            {
              mchCode: 'FPSS9487595950',
              mchType: '并机组分机3',
              mchTotal: 1,
              mchState: '正常运行',
              mchPosition: '成都市武侯区武汉大路',
              mchLoadTotal: 3
            }
          ]
        }
      ],
      // 展示并机组分机单选框状态
      checked: false,
      // 遮罩
      loading: false
    }
  },
  computed: {
    mchDataAll () {
      const mchDataAll = []
      this.mchData.forEach((item) => {
        mchDataAll.push(item)
        if (item.hasSub === true && this.checked === true) {
          item.subMch.forEach((e) => {
            mchDataAll.push(e)
          })
        }
      })
      return mchDataAll
    },
    // 展示并机组分机单选框是否可选，若无并机组则不可选择
    disabled () {
      let flag = true
      this.mchData.map((item) => {
        if (item.hasSub) flag = false
      })
      return flag
    }
  },
  methods: {
    // 故障诊断点击事件
    operatingHandler (scope) {
      this.$message(`点击了故障诊断，机组编码为：` + scope.mchCode)
    },
    // 查看详细数据按钮点击事件
    checkDetailHandler () {
      this.$message(`点击了查看详细数据`)
      // this.$router.push('/engineering/engineeringmanage/projectMonitoringDetail')
      this.$emit('checkDetailHandler')
    },
    // 搜索机组点击事件
    searchHandler (data) {
      this.$message(`点击了搜索`)
      console.log(`点击了搜索`, data)
    }
    // 向服务器请求机组数据（根据筛选条件）
  }
}
</script>

<style lang="less" scoped>
// 标题前图标的颜色
@color-title: #1abc9c;
// 淡灰色字体
@font-style-gray: #999999;
// 统一边距
.padding {
  padding: 0 0 0 15px;
}
// 大数字
.bigNum {
  font-size: 1.8rem;
}
// 卡片中的大文字
.bigFont {
  font-size: 1.2rem;
}
#projectMonitoring {
  margin-bottom: 24px;
  // 1.选择项目
  .project-select {
  }
  // 2.项目数据
  .project-monitoring {
    // 小标题样式
    .title {
      .padding();
      margin: 10px 0;
      &::before {
        left: 0;
        content: "";
        width: 10px;
        height: 10px;
        position: absolute;
        border-radius: 10px;
        background: @color-title;
        transform: translateY(50%);
      }
    }
    > .title {
      .padding();
      height: 45px;
      margin: 10px 0 0 0;
      background: #f3f3f3;
      border: 1px solid #e4e4e4;
    }
    .project-data {
      .card-box-head {
        // 卡片间隔
        > .el-col {
          padding: 10px;
          .el-card {
            height: 100px;
            .el-col {
              display: flex;
              justify-content: center;
            }
          }
        }
        &-label {
          .el-col {
            &:nth-child(1) {
              color: @font-style-gray;
              .bigFont();
            }
            &:nth-child(2) {
              margin: 10px 0;
              .bigNum();
            }
          }
        }
        &-label2 {
          .el-col {
            &:nth-child(1) {
              color: #999999;
              .bigNum();
            }
            &:nth-child(2) {
              margin: 10px 0;
              color: @font-style-gray;
              .bigFont();
            }
          }
        }
      }
      .card-box-bottom {
        > .el-col .el-card {
          height: 120px;
        }
        &-label {
          > .el-col {
            &:nth-child(1) {
              color: #999999;
              .bigFont();
            }
            &:nth-child(2) {
              margin: 10px 0;
              .bigNum();
            }
            &:nth-child(3) {
              color: @font-style-gray;
            }
          }
        }
        > div:last-child .el-card {
          /deep/ .el-card__body {
            padding: 0;
          }
          /deep/ .el-table .cell {
            min-height: 20px;
            line-height: 20px;
          }
          // 变更滚动条样式
          /deep/::-webkit-scrollbar-thumb {
            background-color: #a4c2fe;
          }
        }
      }
    }
  }
  // 3.机组运行数据
  .operating-data {
    .mch-info-table {
      .padding();
    }
  }
}
</style>



