<!--
    @author Demon3443002624@qq.com
    @version 1.0 2022-1-12 09:11:42
    @description 机组筛选组件，无机组列表，仅做筛选（暂仅用于工程-工程管理-项目监控功能）
 -->
<template>
  <div class="toggle-crew-no-list">
    <el-row type="flex" class="items">
      <el-col :span="5">
        <label class="label"
          >机组编码： <el-input v-model="mchCode" placeholder="请输入机组编码"></el-input
        ></label>
      </el-col>
      <el-col :span="5">
        <label class="label">机组类型： </label>
        <el-select v-model="mchType" :popper-append-to-body="false" placeholder="请选择机组类型">
          <el-option
            v-for="(item, index) in mchTypeOpt"
            :key="index"
            :label="item.label"
            :value="item.value"
          ></el-option>
        </el-select>
      </el-col>
      <el-col :span="5">
        <label class="label">机组位置： </label>
        <position-cascader placeholder="请选择机组所在位置" @positionChange="positionChange" />
      </el-col>
      <el-col :span="5">
        <label class="label">运行状态：</label>
        <el-select
          v-model="operatingState"
          :popper-append-to-body="false"
          placeholder="请选择运行状态"
        >
          <el-option
            v-for="(item, index) in operatingStateOpt"
            :key="index"
            :label="item.label"
            :value="item.value"
          ></el-option>
        </el-select>
      </el-col>
      <el-col :span="4">
        <el-button type="primary" @click="searchHandler">搜索</el-button>
      </el-col>
    </el-row>
  </div>
</template>

<script>
import PositionCascader from '@/components/PositionCascader'

export default {
  name: 'ToggleCrewNoList',
  components: {
    PositionCascader
  },
  data() {
    return {
      mchCode: null,
      mchType: null,
      mchPosition: null,
      operatingState: null,
      mchTypeOpt: [],
      operatingStateOpt: []
    }
  },
  mounted() {
    this.getMchTypeOpt()
    this.getOperatingStateOpt()
  },
  methods: {
    // 切换项目点击事件
    searchHandler() {
      const { mchCode, mchType, mchPosition, operatingState } = this.$data
      this.$emit(`searchHandler`, { mchCode, mchType, mchPosition, operatingState })
    },
    // 选择了位置
    positionChange(data) {
      this.mchPosition = data
    },
    // 获取机组类型选项：可能从服务器获取
    getMchTypeOpt() {
      this.mchTypeOpt = [
        {
          label: `单机组`,
          value: 1
        },
        {
          label: `并机组`,
          value: 2
        }
      ]
    },
    // 获取运行状态选择：可能从服务器获取
    getOperatingStateOpt() {
      this.operatingStateOpt = [
        {
          label: `报警`,
          value: 0
        },
        {
          label: `警告`,
          value: 1
        },
        {
          label: `运行`,
          value: 2
        },
        {
          label: `停机`,
          value: 3
        },
        {
          label: `离线`,
          value: 4
        }
      ]
    }
  }
}
</script>

<style lang="less" scoped>
// 统一内边距
.padding {
  padding: 0 0 0 15px;
}
// label 标签显示手型
label {
  cursor: pointer;
}
.title {
  .padding();
  height: 45px;
  background: #f3f3f3;
  border: 1px solid #e4e4e4;
}
.value {
  .padding();
  height: 40px;
}
.items {
  .padding();
  > div {
    display: flex;
    margin-right: 8px;
    .label {
      display: flex;
      align-items: center;
      white-space: nowrap;
      justify-content: center;
    }
  }
  div:last-child {
    align-items: center;
  }
}
</style>



