 * @Description：*** 弃用，新页面在@/views/projectMonitoring
-->
<template>
  <div>
    <BMainTopComponent />
    <ProjectMonitor v-if="pageFlag === 1" @checkDetailHandler="checkDetailHandler" />
    <projectMonitoringDetail v-else />
  </div>
</template>

<script>
import ProjectMonitor from './projectMonitor'
import projectMonitoringDetail from './projectMonitoringDetail'

export default {
  name: 'projectMonitoring',
  components: {
    ProjectMonitor,
    projectMonitoringDetail
  },
  data() {
    return {
      pageFlag: this.$route.query.pageFlag || 1
    }
  },
  watch: {
    $route: {
      handler: function (newVal, oldVal) {
        // console.log(newVal, oldVal)
        this.pageFlag = this.$route.query.pageFlag || 1
      },
      // 深度观察监听
      deep: true
    }
  },
  methods: {
    checkDetailHandler() {
      this.pageFlag = 2
      // this.$router.push({ path: '/syshomePage/home', query: { type: 4, pageFlag: 2 } })
    }
  }
}
</script>



