<!--
 * @Author: linxu
 * @Date: 2021-11-23 09:45:59
 * @LastEditors: linxu
 * @LastEditTime: 2022-05-12 16:51:13
 * @Description: 管理系统页面
-->
<template>
  <div id="system-manage">
    <!-- 管理系统 -->
    <div class="warning-box" @click="warningClickHandler">
      <el-carousel
        ref="carousel"
        height="48px"
        :interval="3000"
        direction="vertical"
        indicator-position="none"
      >
        <!-- 报警信息 -->
        <el-carousel-item v-for="(aItem, aIndex) in warningdatas.d1" :key="'warningKey' + aIndex">
          <el-row align="middle" justify="center" type="flex">
            <img class="img-warning" src="@/assets/img/screenData/Warning-dataScreen.svg" />
            <span>{{ aItem }}</span>
          </el-row>
        </el-carousel-item>
        <!-- 保养提醒 -->
        <el-carousel-item v-for="(rItem, rIndex) in warningdatas.d2" :key="'warningKey2' + rIndex">
          <el-row align="middle" justify="center" type="flex">
            <img class="img-warning" src="@/assets/img/screenData/Repair.svg" />
            <span>{{ rItem }}</span>
          </el-row>
        </el-carousel-item>
      </el-carousel>
    </div>
    <div class="system-manage-content">
      <div class="crew-monitor-item">
        <div class="content-title">机组监控</div>
        <ul class="item-content">
          <li v-for="item in tabs" :key="item.status">
            <span>{{ item.value }}</span>
            <div>{{ item.label }}</div>
          </li>
          <!-- <li><span>0</span><div>报警</div></li>
          <li><span>0</span><div>警告</div></li>
          <li><span>0</span><div>运行</div></li>
          <li><span>0</span><div>停机</div></li>
          <li><span>0</span><div>离线</div></li>
          <li><span>0</span><div>全部</div></li> -->
        </ul>
      </div>
      <div class="contract-overview-item">
        <div class="content-title">合同总览</div>
        <ul class="item-content">
          <li>
            <span>0</span>
            <div>已到期</div>
          </li>
          <li>
            <span>0</span>
            <div>即将到期</div>
          </li>
          <li>
            <span>0</span>
            <div>今日新增</div>
          </li>
          <li>
            <span>0</span>
            <div>本月新增</div>
          </li>
          <li>
            <span>0</span>
            <div>合同总数</div>
          </li>
        </ul>
      </div>
      <div class="pending-transactions-item">
        <div class="content-title">待处理事务</div>
        <ul class="item-content">
          <li><span>剩余7天即将到期合同</span><span>(0)</span></li>
          <li><span>待处理维修单</span><span>(0)</span></li>
          <li><span>待查看《用户满意度调查》</span><span>(0)</span></li>
          <li><span>已到期合同</span><span>(0)</span></li>
          <li><span>机组保养提醒</span><span>(0)</span></li>
          <li><span>已发货订单</span><span>(0)</span></li>
          <li><span>机组日常点检提醒</span><span>(0)</span></li>
        </ul>
      </div>
    </div>

    <table-dialog-warning
      v-if="showTabledialog"
      :showTabledialog="showTabledialog"
      @hiddenTabledialog="hiddenTabledialog"
    />
  </div>
</template>

<script>
import { mapGetters } from 'vuex'
import TableDialogWarning from './components/TabledialogWarning'
// 引入公共方法
import { formatTime } from '@/utils/common'
export default {
  name: 'systemManage',
  components: {
    TableDialogWarning
  },
  data() {
    return {
      animation: null,
      tabs: [
        { type: 'GensetAlarm', status: 2, label: '报警', value: 0 },
        { type: 'GensetWarn', status: 3, label: '警告', value: 0 },
        { type: 'GensetRun', status: 4, label: '运行', value: 0 },
        { type: 'GensetStop', status: 5, label: '停机', value: 0 },
        { type: 'GensetOffline', status: 6, label: '离线', value: 0 },
        { type: 'GensetAll', status: 0, label: '全部', value: 0 }
      ], // 0 所有 2报警 3警告 4运行 5 停机 6离线
      // 实时报警数据
      warningdatas: [],
      realTimeData: {},
      // 弹窗表格数据
      warningDataTable: [],
      keepDataTable: [],
      showTabledialog: false
    }
  },
  computed: {
    ...mapGetters(['selectCrew'])
  },
  mounted() {
    this.getCrewStatusCount()
    this.setWarnKeepData()
    this.animation = setInterval(() => {
      this.getCrewStatusCount()
      this.setWarnKeepData()
    }, 1000 * 60)
  },
  methods: {
    getCrewStatusCount() {
      // 获取统计数据
      this.$api.equipment_old.findGenSetStatusCount().then((res) => {
        if (res.IsSuccess) {
          const data = res.Data
          this.tabs.forEach((item) => {
            item.value = data[item.type]
          })
        }
      })
    },
    setWarnKeepData() {
      // 获取实时数据信息
      this.$api.screenData
        .FindGeneratorSetRadioIndexPage()
        .then((res) => {
          // console.log(res)
          if (res) {
            const data = res.Data
            const alarmList = data.AlarmList || []
            const remindList = data.RemindList || []
            const warningData = []
            const keepData = []
            alarmList.forEach((e, i) => {
              const obj = {
                order: i,
                time: formatTime(e.Time).format('yyyy-MM-DD HH:mm:ss'),
                mchname: e.GensetName,
                divisionmch: '测试分机组',
                warningcontent: e.Content,
                warningtype: e.Type,
                operation: '查看'
              }
              warningData.push(obj)
            })
            remindList.forEach((e, i) => {
              const obj = {
                order: i,
                unit: e.GensetName,
                plandate: formatTime(e.Time).format('YYYY-MM-DD'),
                type: e.Type,
                operation: '查看',
                keepContent: e.Content
              }
              keepData.push(obj)
            })
            this.warningdatas = {
              d1: [
                ...warningData.map((item) => {
                  let msg = '实时报警：' + item.mchname + '-' + item.warningcontent
                  return msg
                })
              ],
              d2: [
                ...keepData.map((item) => {
                  let msg = '保养提醒：' + item.unit + item.keepContent
                  return msg
                })
              ]
            }
            this.warningDataTable = warningData
            this.keepDataTable = keepData
          }
        })
        .catch((err) => {
          console.log(err)
        })
    },

    warningClickHandler() {
      this.showTabledialog = true
    },
    hiddenTabledialog() {
      this.showTabledialog = false
    }
  },
  beforeDestroy() {
    clearInterval(this.animation)
  }
}
</script>

<style lang="less" scoped>
#system-manage {
  height: 100%;
  display: flex;
  flex-direction: column;
  // 警告栏
  .warning-box {
    height: 48px;
    width: 100%;
    overflow: hidden;
    font-size: 1.3rem;
    background-image: url('../../assets/img/screenData/u411.svg');
    .img-middle {
      width: 100%;
      height: 48px;
    }
    .el-row {
      width: 100%;
      height: 100%;
      justify-content: flex-start;
      cursor: pointer;
      span {
        flex: 1;
        padding-right: 10px;
        overflow: hidden;
        text-overflow: ellipsis;
        white-space: nowrap;
      }
    }
    .img-warning {
      width: 28px;
      margin: 0 5px 0 10px;
    }
    // 隐藏触发器
    ul {
      visibility: hidden;
    }
  }

  .system-manage-content {
    display: flex;
    flex-wrap: wrap;
    > div {
      border: 1px solid #e4e4e4;
      box-sizing: border-box;
      margin-bottom: 24px;
      .content-title {
        font-weight: 700;
        border: 0;
        border-bottom: 1px solid #e4e4e4;
        margin-bottom: 0;
      }
      .item-content {
        padding: 16px 0;
        display: flex;
        justify-content: space-around;
        align-items: center;
        li {
          text-align: center;
        }
      }
    }
    .crew-monitor-item,
    .contract-overview-item {
      width: calc(50% - 12px);
    }
    .crew-monitor-item {
      margin-right: 24px;
    }
    .pending-transactions-item {
      width: 100%;
      .item-content {
        position: relative;
        flex-wrap: wrap;
        padding: 24px;
        justify-content: flex-start;
        li {
          display: block;
          width: calc(33.33% - 16px);
          display: flex;
          padding: 0 8px 8px;
          flex-wrap: wrap;
          justify-content: space-between;
          box-sizing: border-box;
          border-bottom: 1px solid rgb(242, 242, 242);
          margin: 0 24px 16px 0;
        }
        li:nth-child(3n) {
          margin-right: 0;
        }
      }
    }
  }
}
</style>



