-->
<template>
  <div>
    <BMainTopComponent :isShowBackBtn="isShowBackBtn" />
    <div id="home-page">
      <div class="tab-box">
        <el-button
          v-for="item in tabs"
          :key="item.value"
          :class="['status-tab', { 'select-tab': selectTab == item.value }]"
          @click="changeTab(item.value)"
          >{{ item.label }}</el-button
        >
      </div>
      <!-- 管理系统 -->
      <system-manage v-if="selectTab == 1" />
      <!-- 机组监控 -->
      <crew-monitor v-if="selectTab == 2" class="crew-monitor-info" />
      <!-- 传感器监控 SensorMonitor -->
      <sensor-monitor v-if="selectTab == 3" :isShow="false" class="sensor-monitor-info" />
      <!-- 项目监控 ProjectMonitor -->
      <project-monitor v-if="selectTab == 4" class="project-monitor-info" />
    </div>
  </div>
</template>

<script>
import ToggleCrew from '@/components/toggleCrew'
import systemManage from './systemManage'
import sensorMonitor from '@/views/operation/maintenanceMonitor'
import projectMonitor from './projectMonitoring'
import CrewMonitor from '@/views/equipmentManage/crew-monitor'
import { mapGetters } from 'vuex'

export default {
  name: 'home',
  components: {
    ToggleCrew,
    systemManage,
    sensorMonitor,
    projectMonitor,
    CrewMonitor
  },

  data() {
    return {
      tabs: [
        { label: '运维统计', value: 1, linkUrl: '' },
        {
          label: '机组监控',
          value: 2,
          linkUrl: ''
        },
        { label: '传感器监控', value: 3, linkUrl: '' }
        // { label: '项目监控', value: 4, linkUrl: '' }
      ],
      selectTab: 1,
      isShowBackBtn: !!(this.$route.query.type === '4' && this.$route.query.pageFlag === '2')
    }
  },
  computed: {
    ...mapGetters(['selectCrew']),
    linkUrl() {
      if (this.selectTab === 2) {
        return this.tabs.filter((item) => item.value === this.selectTab)[0].linkUrl
      }
    }
  },
  watch: {
    $route: {
      handler: function (newVal, oldVal) {
        // console.log('$route', newVal)
        if (newVal.query.type) {
          this.selectTab = newVal.query.type
        } else {
          this.selectTab = 1
        }
        if (newVal.query.type === '4' && newVal.query.pageFlag === '2') {
          this.isShowBackBtn = true
        } else {
          this.isShowBackBtn = false
        }
      },
      // 深度观察监听
      deep: true
    }
  },
  mounted() {
    if (this.$route.query.type) {
      this.selectTab = this.$route.query.type
    }
  },
  methods: {
    changeTab(num) {
      this.$router.push({ path: `/operation/operationStatistics`, query: { type: num } })
    }
  }
}
</script>

<style lang="less" scoped>
#home-page {
  .tab-box {
    margin-bottom: 16px;
    .select-tab {
      background-color: rgba(26, 188, 156, 1) !important;
      color: #fff !important;
      border: 0;
    }
  }
  .el-tabs.home-tabs {
    flex: 1;
    position: relative;
    > .el-tabs__content {
      position: absolute !important;
      width: 100%;
      // height: 100%;
      overflow: auto;
      .el-tab-pane {
        // height: 100%;
      }
    }
  }

  /deep/ .crew-monitor-info {
    #crew-monitor {
      display: flex;
      flex-direction: column;
      height: 100%;
    }
  }
  /deep/ .crew-monitor-info,
  /deep/ .sensor-monitor-info,
  /deep/ .project-monitor-info {
    height: calc(100% - 48px);
    .el-container {
      height: 100%;
      padding: 0;
    }
  }
}
</style>



