<!--
    @author Demon3443002624@qq.com
    @version 1.0 2022-1-12 09:11:42
    @description 横向列表（暂仅用于工程-工程管理-项目监控功能）
 -->
<template>
  <div class="transverse-table">
    <div class="head" type="flex" align="middle">
      <div class="head-box"></div>
      <!-- <div>机组类型</div>
      <div>机组数</div>
      <div>运行状态</div>
      <div>机组位置</div>
      <div>待载个数</div> -->
      <div>优先级</div>
      <div>可用状态</div>
      <div>机组位置</div>
      <div>最大功率</div>
      <div>限制功率</div>
      <div>投入状态</div>
      <div>机组寿命</div>
      <div>平均无故障时间</div>
      <div>待载个数</div>
    </div>
    <div class="items">
      <div class="item" v-for="(item, index) in mchData" :key="index">
        <div class="head-box">{{ item.code }}</div>
        <div v-for="(e, i) in item.data" :key="i">{{ e }}</div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'TransverseTable',
  data() {
    return {
      stateColor: {
        报警: '#f1534f',
        警告: '#f2c614',
        正常运行: '#20be62',
        停机: '#f1534f',
        离线: '#808080',
        全部: '#000000'
      }
    }
  },
  props: {
    mchData: {
      type: Array,
      default: (_) => {
        return []
      }
    }
  }
}
</script>

<style lang='less' scoped>
@color-border: #e7e7e7;
@width-head: 150px;
.transverse-table {
  height: 340px;
  display: flex;
  border: 1px solid @color-border;
  // 变更滚动条样式
  /deep/::-webkit-scrollbar-thumb {
    background-color: #cddbf7;
  }
  .head-box {
    background: #e4eaf4;
  }
  .head {
    width: @width-head;
    height: 100%;
    display: flex;
    flex-direction: column;
    border-right: 1px solid @color-border;
    > div {
      flex: 1;
      display: flex;
      font-weight: bold;
      align-items: center;
      justify-content: center;
      border-bottom: 1px solid @color-border;
      &:last-child {
        border-bottom: 0;
      }
    }
  }
  .items {
    height: 100%;
    width: calc(100% - @width-head);
    display: flex;
    flex-wrap: nowrap;
    overflow: auto;
    .item {
      height: 100%;
      width: 180px;
      min-width: 180px;
      display: flex;
      flex-direction: column;
      border-right: 1px solid @color-border;
      > div {
        flex: 1;
        display: flex;
        align-items: center;
        justify-content: center;
        border-bottom: 1px solid @color-border;
        &:last-child {
          border-bottom: 0;
        }
      }
    }
  }
}
</style>



