<!--
 * @Author: Demon3443002624@qq.com
 * @Date: 2021-11-17 11:30:45
 * @Description: 油耗历史记录列表数据
-->
<template>
  <!-- <div class="TabledialogWarning">
    <el-button type="text" @click="clickHandler"></el-button> -->

  <el-dialog
    title="提示信息"
    class="Tabledialog"
    :center="true"
    :visible.sync="dialogTableVisible"
    :modal-append-to-body="false"
    :close-on-click-modal="false"
    :close-on-press-escape="false"
    @close="closeDialog"
  >
    <el-tabs v-model="activeName" @tab-click="handleClick">
      <el-tab-pane label="当前报警" name="first">
        <!-- 表格 -->
        <el-table
          ref="warningTable"
          :data="warningdatas"
          size="medium"
          style="width: 100%"
          height="500px"
          :highlight-current-row="false"
        >
          <el-table-column type="index" align="center" label="序号" width="60px"> </el-table-column>
          <el-table-column
            v-for="value in waringcolumn"
            :min-width="value.width"
            :label="value.label"
            :key="value.key"
            align="center"
            :prop="value.prop"
          >
          </el-table-column>
          <el-table-column width="60px" label="操作" key="title8" align="center">
            <template slot-scope="scope">
              <el-button @click="handleClick_check(scope.row, 0)" type="text" size="medium"
                >查看</el-button
              >
            </template>
          </el-table-column>
        </el-table>
      </el-tab-pane>
      <el-tab-pane label="保养信息" name="second">
        <!-- 表格 -->
        <el-table
          ref="keepTable"
          :data="keepdatas"
          size="medium"
          style="width: 100%"
          height="500px"
          :highlight-current-row="false"
        >
          <el-table-column type="index" align="center" label="序号" width="60px"> </el-table-column>
          <el-table-column
            v-for="value in keepcolumn"
            :min-width="value.width"
            :label="value.label"
            :key="value.key"
            :prop="value.prop"
            align="center"
          >
          </el-table-column>
          <el-table-column width="60px" label="操作" key="title6" align="center">
            <template slot-scope="scope">
              <el-button @click="handleClick_check(scope.row, 1)" type="text" size="medium"
                >查看</el-button
              >
            </template>
          </el-table-column>
        </el-table>
      </el-tab-pane>
    </el-tabs>
  </el-dialog>

  <!-- </div> -->
</template>

<script>
import data from '../data'
import { formatTime } from '@/utils/common'
import { mapActions } from 'vuex'
export default {
  props: ['showTabledialog'],
  data() {
    return {
      activeName: 'first',
      dialogTableVisible: true,
      warningdatas: [],
      waringcolumn: data.warninglist.waringcolumn,
      keepdatas: [],
      // keepdatas: [],
      keepcolumn: data.warninglist.keepcolumn,
      // 实时报警数据
      warningDataTable: [],
      // 保养数据
      keepDataTable: []
    }
  },
  watch: {
    warningDataTable(val, old) {
      this.warningdatas = val
    },
    keepDataTable(val, old) {
      this.keepdatas = val
    }
  },
  mounted() {
    this.init()
  },
  methods: {
    ...mapActions(['toggleSelectCrew']),
    handleClick(tab, event) {
      const ref = this.activeName === 'first' ? 'warningTable' : 'keepTable'
      setTimeout(() => {
        this.$refs[ref].doLayout()
      }, 100)
    },
    handleClick_check(row, type, event) {
      // console.log(row, type)
      if (row.id) {
        const query = {
          // IsWeb: true,
          page: 0,
          pageSize: 999
        }
        const params = {
          model: {
            DisplayMode: 1, // 1为所有启用，0为所有机组 2为所有禁用
            IsLease: 1, // 租赁
            UserID: -1,
            CollectorToken: '', // 标识
            StatusMode: 0 // 0 所有 2报警 3警告 4运行 5 停机 6离线
          }
        }
        this.$api.common.getCrewList(params, query).then((res) => {
          const data = res.Data
          const crewinfo = data.Data.filter((item) => item.ID === row.id)[0]
          if (crewinfo && crewinfo.ID) {
            this.toggleSelectCrew(crewinfo)
            if (type === 0) {
              this.$router.push('/equipment/crewmonitor/crew-monitor-info')
            } else {
              this.$router.push('/operation/unit-maintenance-manage/maintain-record')
            }
          } else {
            this.$message({
              type: 'error',
              message: '未查询到该机组！'
            })
          }
        })
      }
    },
    init() {
      // 清空列表数据，以重载
      this.warningdatas = []
      this.keepdatas = []
      const loading = this.$loading({
        lock: true,
        text: '数据加载中，请勿操作...',
        spinner: 'el-icon-loading',
        background: 'rgba(0, 0, 0, 0.7)'
      })
      this.setWarnKeepData((res) => {
        loading.close()
      })
    },
    setWarnKeepData(cb) {
      this.$api.screenData.FindGeneratorSetRadioIndexPage().then((res) => {
        if (res) {
          const data = res.Data
          const alarmList = data.AlarmList || []
          const remindList = data.RemindList || []
          const warningData = []
          const keepData = []
          alarmList.forEach((e, i) => {
            const obj = {
              order: i,
              id: e.GeneratorSetId,
              time: formatTime(e.Time).format('yyyy-MM-DD HH:mm:ss'),
              mchname: e.GensetName,
              divisionmch: '测试分机组',
              warningcontent: e.Content,
              warningtype: `警告`,
              // warningtype: e.Type,
              operation: '查看'
            }
            warningData.push(obj)
          })
          remindList.forEach((e, i) => {
            const obj = {
              order: i,
              id: e.GeneratorSetId,
              unit: e.GensetName,
              plandate: formatTime(e.Time).format('YYYY-MM-DD'),
              type: `保养`,
              // type: e.Type,
              keepContent: e.Content,
              operation: '查看'
            }
            keepData.push(obj)
          })
          this.warningdatas = [
            ...warningData.map((item) => '实时报警：' + item.mchname + '-' + item.warningcontent),
            ...keepData.map((item) => '保养提醒：' + item.unit + item.keepContent)
          ]
          // 排序最新的在最前面
          warningData.length > 0 &&
            warningData.sort((a, b) => {
              const res = this.$moment(a.time).diff(this.$moment(b.time))
              return -res
            })
          keepData.length > 0 &&
            keepData.sort((a, b) => {
              const res = this.$moment(a.plandate).diff(this.$moment(b.plandate))
              return -res
            })
          this.warningDataTable = warningData
          this.keepDataTable = keepData
        }
        cb(res)
      })
    },
    closeDialog() {
      this.$emit('hiddenTabledialog')
    }
  }
}
</script>
<style lang="less" scoped>
.Tabledialog {
  // table 的左右间距
  @sideinterval: 40px;
  // table 透明度（影响了文字）
  @tableopacity: 0.95;
  .el-button {
    width: 100%;
  }
  .el-dialog--center .el-dialog__body {
    padding: 30px 40px !important;
  }
  // 重写 tab 标签
  .el-tabs__item {
    padding: 0 20px !important;
    background: #eee;
  }
  .el-tabs__item[class*='is-active'] {
    background-color: #1abc9c;
    columns: white;
    padding: 0 20px !important;
  }
  .el-tabs__item.is-active {
    color: #fff;
  }
  .el-tabs__nav-wrap::after {
    background-color: transparent;
  }
  .el-dialog .el-dialog__header {
    background-color: #1abc9c;
    opacity: 0.8;
  }
  .el-dialog--center .el-dialog__body {
    text-align: initial;
    padding: 0 @sideinterval 30px @sideinterval;
    opacity: @tableopacity;
  }
  .el-table,
  .el-table__expanded-cell {
    background-color: transparent;
  }
  .el-table__cell:first-child .cell {
    white-space: nowrap;
  }
}
</style>



